import React from 'react';
import moment from 'moment';
import { withRouter } from 'react-router-dom';
import { Card, Form, DatePicker, Input, Button, Icon, Row, Col, Tag, message } from 'antd';
import ChooseStrategyTargetModal from './ChooseStrategyTargetModal';
import BackModal from './BackModal';
import styles from '../index.module.less';

const FormItem = Form.Item;
const RangePicker = DatePicker.RangePicker;
const formItemLayout = {
    labelCol: { span: 10},
    wrapperCol: { span: 14, style:{paddingLeft:'10px'}},
};

const studentStyle = { 
    display:'flex',alignItems:'center',justifyContent:'center',
    width:258,height:58,marginTop:-10,lineHeight:'20px',textAlign:'center',
    borderRadius:4,border:'1px dashed #d9d9d9',cursor:'pointer',
}

const disabledDate = (current)=> {
    return current && current < moment().endOf('day');
}

const dateFormat = 'YYYY-MM-DD';

class AddOrEdit extends React.Component {
    state = {
        showChoose: false,
        showBack: false,
        selectedTarget: null,
        editId: "0",
        editInfo: null,
        rangePickerValue: [],
        strategyDate:[]
    }

    componentDidMount(){
        if(this.props.match.params && this.props.match.params.id !== '0'){
            let res = {"success":true,"msg":"成功","obj":{"id":"f3d122e652ba4b49867c78565733611e","updateDate":"2019-08-25","strategyDate":"2019-09-05~2019-09-20","remark":"test","strategyTarget":"100225","strategyName":"测试","strategyTargetCollege":"电子科学与工程学院"},"errorCode":null};
            if (res.success) {

                let editInfo = Object.assign({},res.obj) || {};
                let selectedTarget = {strategyName: editInfo.strategyName}

                let strategyDate = editInfo.strategyDate && editInfo.strategyDate.split(',').map(item=>{
                    return {
                        startDate: item.split('~')[0],
                        endDate: item.split('~')[1]
                    }
                })
                
                let strategyTargetCollege = editInfo.strategyTargetCollege?editInfo.strategyTargetCollege.split('、'):[];

                this.setState({
                    editInfo,
                    selectedTarget,
                    strategyDate,
                    editId:this.props.match.params.id,
                })
                this.props.form.setFieldsValue({
                    strategyDate,
                    strategyTargetCollege
                })
            }

            // postAction("/bigdata/alarmHolidayWhitelist/findById", {id:params.id}).then(
            //     (res) => {
            //         if (res.success) {}
            //     }
            // )
        }
    }

    chooseStrategy = (selectedTarget) => {
        this.setState({selectedTarget,showChoose:false})
        let strategyTargetCollege = selectedTarget.strategyTargetCollegeName?selectedTarget.strategyTargetCollegeName.split('、'):[];
        this.props.form.setFieldsValue({
            strategyId: selectedTarget.id,
            strategyTargetCollege
        })
    }

    changeRangePicker = (values)=>{
        this.setState({rangePickerValue:values})
    }

    addStrategyDate = () => {
        let { rangePickerValue } = this.state;
        if(rangePickerValue && rangePickerValue.length > 0){
            let { strategyDate } = this.state;
            let addDate = {
                startDate: rangePickerValue[0].format(dateFormat),
                endDate: rangePickerValue[1].format(dateFormat)
            }
            let isExist = false;
            strategyDate.forEach(item=>{
                // 判断日期不互相包含才行
                if(
                    (moment(item.startDate)>moment(addDate.startDate) && 
                    moment(item.startDate)>moment(addDate.endDate)) || 
                    (moment(item.endDate)<moment(addDate.startDate) && 
                    moment(item.endDate)<moment(addDate.endDate))                    
                    ){
                    isExist = false 
                }else{
                    isExist = true 
                }
                
            })
            if(isExist){
                message.error('所选日期重复！');
                return;
            }
            // 合并选择的到state
            strategyDate=[...strategyDate, addDate]
            this.setState({
                strategyDate,
                rangePickerValue: []
            })
            this.props.form.setFieldsValue({strategyDate})            
        } else {
            message.error('请选择日期范围！')
        }
        
    }

    deletestrategyDate = (item)=> {
        let strategyDate = JSON.parse(JSON.stringify(this.state.strategyDate));
        let changestrategyDate = strategyDate.filter((a)=>{
            return !(a.startDate===item.startDate && a.endDate===item.endDate)
        });
        this.setState({
            strategyDate: changestrategyDate
        })
        this.props.form.setFieldsValue({
            strategyDate: changestrategyDate
        })
    }

    saveAction = () => {
        this.props.form.validateFields(
            (err,values) => {
                if (!err) {
                    this.props.history.goBack();
                    // const { strategyId, remark } = values;
            
                    // if(this.state.strategyDate.length === 0){
                    //     message.error('请选择不预警时间');
                    //     return false;
                    // }
                    
                    // let strategyDate = this.state.strategyDate.map(item=>{
                    //     return item.startDate + '~' + item.endDate;
                    // }).join(',');
                    
                    // if(this.state.editInfo){ //编辑时
                    //     let data = {  
                    //         id: strategyId,                       
                    //         remark,
                    //         strategyDate
                    //     }
                    //     postAction("/bigdata/alarmHolidayWhitelist/update", data).then(
                    //         (res) => {
                    //             if (res.success) {
                    //                 message.success('修改成功！',1,()=>{
                    //                     this.props.history.goBack();
                    //                 });
                    //             } else {
                    //                 message.warn(res.obj);
                    //             }
                    //         }
                    //     ) 
                    // }else{
                    //     let data = {
                    //         strategyId,
                    //         strategyTarget:this.state.selectedTarget.strategyTarget,
                    //         remark,
                    //         strategyDate,
                    //     }
                    //     postAction("/bigdata/alarmHolidayWhitelist/save", data).then(
                    //         (res) => {
                    //             if (res.success) {
                    //                 message.success('保存成功！',1,()=>{
                    //                     this.props.history.goBack();
                    //                 });
                    //             } else {
                    //                 message.warn(res.obj);
                    //             }
                    //         }
                    //     ) 
                    // }                    
                }
            },
        );
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        let { selectedTarget, editId, editInfo, strategyDate } = this.state;
        let strategyTargetCollege = this.props.form.getFieldValue('strategyTargetCollege') || [];
        return (
            <Card
                className={styles.mainCard}
                title={<div className={styles.cardTitle}>{editId === '0' ? '新增节假日白名单配置' : '编辑节假日白名单配置'}</div>}
                bordered={false}
                bodyStyle={{padding: '35px 24px'}}
                style={{margin: 20}}
            >
                <FormItem {...formItemLayout} label="选择关联的预警策略">
                    {getFieldDecorator('strategyId', {
                        rules: [{
                            required: true,
                            message: '请选择关联的预警策略！'
                        }],
                        initialValue: editInfo && editInfo.id,
                    })(
                        <div style={studentStyle} onClick={()=>this.setState({showChoose:true})}>
                            {
                                selectedTarget ? 
                                <React.Fragment>
                                    {selectedTarget.strategyName}
                                </React.Fragment>
                                :
                                <React.Fragment>
                                    <Icon type="plus" /> 选择关联的预警策略
                                </React.Fragment>
                            }
                        </div>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="不预警学院">
                    {getFieldDecorator('strategyTargetCollege', {
                        rules: [{
                            type: 'array',
                        }]
                    })(
                        <React.Fragment>
                            {strategyTargetCollege.map((item,index)=>(
                                <Tag 
                                    style={{height:28,lineHeight:'28px',color:'#4a4a4a'}}
                                    key={index}>{item}</Tag>
                            ))}
                        </React.Fragment>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="选择不预警时间">
                    {getFieldDecorator('strategyDate', {
                        rules: [{
                            type: 'array',
                            message: '请选择不预警时间！'
                        }]
                    })(
                        <React.Fragment>
                            <RangePicker style={{width:398}} 
                                disabledDate={disabledDate} value={this.state.rangePickerValue} onChange={this.changeRangePicker}/>
                            <Icon type="plus-circle-o" style={{marginLeft:10,cursor:'pointer'}} onClick={this.addStrategyDate} />
                            <br/>
                            {strategyDate.map((item,index)=>(
                                <Tag style={{height:28,lineHeight:'28px',color:'#4a4a4a'}} key={index}>
                                    {item.startDate+'~'+item.endDate} 
                                    <Icon type="close" 
                                        style={{marginLeft:5,color:'rgba(0, 0, 0, 0.45)'}} 
                                        onClick={()=>this.deletestrategyDate(item)}/>
                                </Tag>
                            ))}
                        </React.Fragment>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="备注">
                    {getFieldDecorator('remark',{
                        initialValue: editInfo && editInfo.remark
                    })(
                        <Input.TextArea style={{width:398,height:138,resize:'none'}} maxLength="50" />
                    )}
                </FormItem>
                <Row>
                    <Col {...formItemLayout.labelCol}></Col>
                    <Col {...formItemLayout.wrapperCol}>
                        <Button size="large" type="primary" style={{marginRight:20}} onClick={this.saveAction}>保 存</Button>
                        <Button size="large" onClick={() => {this.setState({showBack: true})}}>返 回</Button>
                    </Col>
                </Row>
                {
                    this.state.showChoose 
                    && 
                    <ChooseStrategyTargetModal
                        visible={this.state.showChoose}
                        selectedTarget={this.state.selectedTarget}
                        onCancel={()=>this.setState({showChoose:false})}
                        onOk={this.chooseStrategy}/>
                }
                {
                    this.state.showBack 
                    && 
                    <BackModal
                        visible={this.state.showBack}
                        onCancel={()=>this.setState({showBack:false})}
                        onOk={() => {this.props.history.goBack()}}/>
                }
            </Card>
        )
    }
}

const AddOrEditForm = Form.create()(AddOrEdit);
export default withRouter(AddOrEditForm);