import React from 'react'
import { Modal, Icon } from 'antd'

export default class BackModal extends React.Component {
    render() {
        return (
            <Modal
                width={500}
                centered
                bodyStyle={{padding: '50px 30px'}}
                visible={this.props.visible}
                onCancel={this.props.onCancel}
                onOk={this.props.onOk}
            >
                <Icon type="question-circle" style={{marginRight: 15, fontSize: 22, color: '#faad14', verticalAlign: 'top'}}/>
                <span style={{fontSize: 16, fontWeight: 500, color: 'rgba(0,0,0,.85)', lineHeight: '22px'}}>确认返回预警白名单设置吗?</span>
                <p style={{marginLeft: 37, fontSize: 14, color: 'rgba(0,0,0,.65)', marginTop: 8}}>当前设置未保存，返回将不会保存已编辑的内容。</p>
            </Modal>
        )
    }
}